.main {
	padding-top: 120px;
	@media(max-width: 1023px) {
		padding-top: 80px;
	}
	@media(max-width: 450px) {
		padding-top: 50px;
	}
	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
		max-width: 1310px;
		margin: 0 auto;
		padding-bottom: 150px;
		padding-right: 15px;
		padding-left: 15px;
		@media(max-width: 1023px) {
			max-width: 950px;
			padding-right: 35px;
			padding-left: 35px;
			padding-bottom: 100px;
		}
		@media(max-width: 960px) {
			width: 100%;
			padding: 0;
			padding-bottom: 100px;
		}
	}
	&__promo-link {
		padding: 15px 115px;
		font-size: 20px;
		font-weight: 500;
		line-height: 25px;
		color: @c-main;
		border-radius: 55px;
		text-transform: lowercase;
		@media(max-width: 1023px) {
			padding: 10px 65px;
			font-size: 18px;
		}
		@media(max-width: 767px) {
			display: none;
		}
		&:hover {
			color: #ffffff;
		}
	}
	&__programs {
		width: 100%;
		margin-top: 135px;
		@media(max-width: 1023px) {
			margin-top: 80px;
		}
		@media(max-width: 767px) {
			margin-top: 0;
		}
	}
}
.programs {
	&__title {
		width: 100%;
		text-align: center;
		font-size: 30px;
		font-weight: 500;
		color: #4e93e1;
		text-transform: uppercase;
		letter-spacing: 0.6px;
		@media(max-width: 1023px) {
			font-size: 24px;
		}
		@media(max-width: 530px) {
			font-size: 16px;
		}
	}
	&__slider {
		margin-top: 100px;
		@media(max-width: 1023px) {
			margin-top: 60px;
		}
		@media(max-width: 450px) {
			margin-top: 40px;
		}
	}
}
.slide-discr,
.slide-discr--promo{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 25px;
	font-size: 24px;
	font-weight: 500;
	text-align: center;
	z-index: 2;
}
.slide-discr--promo {
	display: none;
}
.slide-link {
	position: relative;
	font-size: 0;
	width:100%;
	height: 450px;
	padding: 5px 0;
	box-shadow: 3px 3px 7px rgba(0,0,0,.2);
	z-index: 2;
	transition: 0.5s;
	@media (max-width:960px) {
		transform: scale(0.9);
	}
	&1 {
		background: url('@{bg}program1.jpg') no-repeat top center/cover;
	}
	&2 {
		background: url('@{bg}program2.jpg') no-repeat top center/cover;
	}
	&3 {
		background: url('@{bg}program3.jpg') no-repeat top center/cover;
	}
	&::after {
		content: '';
		position: absolute;
		display: none;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,.15);
		transition: 0.25s;
		z-index: 1;
	}
	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 115px;
		background-color: rgba(0,0,0,.4);
		transition: 0.25s;
		z-index: 1;
	}
	&:hover::after {
		transition: 1s;
		display: block;
	}
	&:hover::before {
		background-color: transparent;
	}
	&:hover>.slide-discr {
		display: none;
	}
	&:hover>.slide-discr--promo {
		display: block;
	}
}
.slick-list {
		width: 100%;
		overflow: hidden;
		@media (max-width:960px) {
			padding: 10px 200px;
		}
		@media (max-width:800px) {
			padding: 0 100px;
		}
		@media (max-width:550px) {
			padding: 0 50px;
		}
		@media (max-width:360px) {
			padding: 0 20px;
		}
	}
.slick-track{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
	margin: 0 -10px;
	@media (max-width:960px) {
		margin: 0;
	}

}
.slick-slide {
	min-width: 260px;
	height: 450px;
	margin: 10px;
	
	@media (max-width:960px) {
		width: 400px;
		height: 450px;
		margin: 0;
	}
}
.slick-active	 {
	transition: 0.5s;
	@media (max-width:960px) {
		height: 450px;
	}
	@media (max-width:550px) {
		width: 260px;
	}
	.slide-link {
		@media (max-width:960px) {
			
			transform: scale(1);
		}
	}
}
.slick-center {
	@media (max-width:550px) {
		width: 260px;
	}
}