.header {
	position: relative;
	font-family: @ff;
	min-height: 100vh;
	background: url('@{bg}bg-header.jpg') no-repeat center/cover;
	@media(max-width: 768px) {
		background: url('@{bg}bg-header-mob.jpg') no-repeat center/cover;
	}
	&__container {
		height: 100%;
	}
	&__logo {
		@media(max-width: 1280px) {
			width: 108px;
			height: 108px;
		}
		@media(max-width: 960px) {
			width: 88px;
			height: 88px;
		}
		@media(max-width: 768px) {
			z-index: 11;
		}
	}
	&__menu {
		display: flex;
		align-items: center;
	}
	&__info {
		display: flex;
		justify-content: flex-end;
		margin-top: 107px;
		padding-bottom: 100px;
		color: #ffffff;
		@media(max-width: 1024px) {
			margin-top: 35px;
		}
		@media(max-width: 768px) {
			padding-bottom: 50px;
		}
		&-inner {
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			width: 50%;
			padding: 10px;
			@media(max-width: 1024px) {
				text-align: center;
			}
			@media(max-width: 960px) {
				width: 60%;
			}
			@media(max-width: 768px) {
				width: 100%;
				min-height: 60vh;
				align-items: center;
				justify-content: space-between;
			}
			@media(max-width: 650px) {
				padding: 0;
			}
		}
		&-btn {
			flex: none;
			margin-top: 52px;
			@media (max-width: 1280px) {
				margin-top: 40px;
			}
			@media(max-width: 650px) {
				margin-top: 80px;
			}
		}
		&-btn-link {
			flex-grow: 1;
			padding: 10px 75px;
			transition: 0.2s;
		}
	}
	&__title {
		font-family: @ff;
		font-size: 42px;
		font-weight: 800;
		line-height: 48px;
		letter-spacing: 1.26px;
		span {
			color: @c-main;
		}
		@media (max-width: 1280px) {
			font-size: 36px;
			line-height: 42px;
			letter-spacing: 0.72px;
		}
		@media(max-width: 650px) {
			font-size: 20px;
			line-height: 24px;
			letter-spacing: 0.2px;
		}
	}
	&__discr {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 66%;
		margin-top: 31px;
		padding: 8px;
		font-family: "Open Sans","Arial", sans-serif;
		font-size: 16px;
		font-weight: 400;
		line-height: 20px;
		color: #f1f1f1;
		@media (max-width: 1280px) {
			width: 81%;
		}
		@media(max-width: 1024px) {
			width: 100%;
		}
		@media(max-width: 768px) {
			min-height: 35vh;
			justify-content: space-between;
		}
		@media(max-width: 450px) {
			padding: 0;
		}
		&::after {
			content: '';
			position: absolute;
			width: 179px;
			height: 65px;
			top: -2px;
			left: -189px;
			background: url('@{bg}header-star.png') no-repeat center/cover;
			@media (max-width: 1280px) {
				display: none;
			}
		}
		&-img {
			width: 35%;
		}
		&__inner {
			margin-left: 15px;
		}
	}
}
.header__top-line {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	padding-top: 10px;
	@media (max-width: 1280px) {
		font-size: 14px;
	}
	@media (max-width: 450px) {
		justify-content: space-around;
	}
}
.logo {
	font-size: 0;
	@media (max-width: 450px) {
		order: 2;
	}
}
.location {
	display: flex;
	justify-content: space-between;
	flex-shrink: 0;
	color: #ffffff;
	letter-spacing: 0.32px;
	@media (max-width: 450px) {
		order: 1;
	}
	&__item {
		margin: 0 18px;
		@media(max-width: 1023px) {
			margin: 0 10px;
		}
	}
	&__phone {
		position: relative;
		flex-shrink: 0;
		padding-left: 20px;
		@media (max-width: 450px) {
			display: none;
		}
		&::after {
			content: '';
			position: absolute;
			width: 14px;
			height: 14px;
			top: 50%;
			left: 0;
			background-image: url('@{bg}phone.png');
			transform: translateY(-50%);
		}
	}
}
.nav {
	@media (max-width: 450px) {
		order: 3;
	}
	&-hidden {
		@media(max-width: 768px) {
			display: none;
		}
	}
	
	&__list {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		margin: 0 -10px;
		@media(max-width: 768px) {
			flex-direction: column;
			align-items: center;
			margin: 0;
			margin-top: 30px;
			padding-bottom: 25px;
			font-size: 16px;
			border-bottom: 1px solid #696969;
		}
	}
	&__list-item {
		margin: 0 18px;
		@media(max-width: 1023px) {
			margin: 0 10px;
		}
	}
	&__list-item--active {
		a {
			border-bottom: 2px solid @c-main;
			cursor: default;
		}
	}
	&__list-link {
		transition: 0.05s;
		@media(max-width: 1280px) {
			font-size: 14px;
		}
		@media(max-width: 768px) {
			width: 100%;
			padding: 14px 20px;
		}
	}
	&__list-link[href]:hover {
		border-bottom: 2px solid @c-main;
		@media(max-width: 768px) {
			border: none;
		}
	}

}
.teacher-link {
	margin: 5px;
	transition: 0.2s;
	@media(max-width: 1280px) {
		padding: 10px 25px;
		font-size: 10px;
	}
	@media(max-width: 768px) {
		margin-top: 10px;
	}
}
.user-block {
	display: flex;
	align-items: center;
	@media(max-width: 768px) {
		flex-direction: column;
		margin-top: 15px;
	}
	@media (max-width: 450px) {
		order: 4;
	}
	&-hidden {
		@media(max-width: 1023px) {
			display: none;
		}
	}
	&__login,
	&__registration {
		margin-left: 33px;
		transition: 0.2s;
		@media(max-width: 1280px) {
			margin: 0;
			padding: 10px;
			font-size: 14px;
			font-weight: 500;
			color: #000000;
		}
		@media(max-width: 768px) {
			margin: 0;
			padding: 15px 20px;
			color: #ffffff;
		}
		&:hover {
			text-shadow: 1px 1px 3px @c-main;
		}
		
		&--hidden {
			@media(max-width: 1280px) {
				display: none;
			}
		}
		
	}
}

.fade-in {
	position: absolute;
	left: 50%;
	bottom: 0;
	transform: translateX(-50%);
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 45px;
	@media(max-width: 768px) {
		display: none;
	}
	img {
		cursor: pointer;
		transition: 0.2s;
		&:hover {
			transform: translateY(4px);
		}
	}
	&__text {
		margin-top: 20px;
		font-size: 12px;
		font-weight: bold;
		line-height: 24px;
		letter-spacing: 0.48px;
		text-transform: uppercase;
		color: #ffffff;
		@media (max-width: 1024px) {
			display: none;
		}
	}
}