.title {
	position: relative;
	padding-top: 43px;
	.ff-700();
	font-size: 30px;
	line-height: 42px;
	text-align: center;
	color: #2c2e37;
	text-transform: uppercase;
	letter-spacing: 3.51px;
	@media(max-width:1200px) {
		padding-top: 25px;
	}
	&::after {
		content:'';
		position: absolute;
		width: 81px;
		height: 7px;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		background: url(../img/sprite.svg#title-line) no-repeat center;
		background-size: 100%;
	}
}