.mobile-user {
	display: none;
	position: absolute;
	top: -1000px;
	right: 0px;
	display: flex;
	flex-direction: column;
	align-items: center;
	min-width: 164px;
	padding: 16px;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	overflow: auto;
	z-index: 10;
	transition: 0.5s;
	&__active {
		top: 100px;
		right: 0;
	}
	@media (max-width: 767px) {
		display: flex;
	}
	background-color: #ffffff;
}