* {
	box-sizing: border-box;
}
body {
	position: relative;
	font-family: @ff;
	font-weight: normal;
	font-size: 16px;
	width: 100%;
	min-width: 320px;
	height: 100%;
	margin: 0;
	line-height: 1.4;
}
h1, h2, h3, h4, h5, h6, ul, p {
	padding: 0;
	margin: 0;
	font-weight: normal;
}
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
.link {
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
	outline: none;
	border: none;
	font-size: 0;
	font-size: 16px;
	vertical-align: top;
	color: #ffffff;
}
.link-btn {
	padding: 10px 35px;
	font-size: 12px;
	font-weight: 500;
	text-transform: uppercase;
	letter-spacing: 0.24px;
	border-radius: 4px;
	&--border {
		border: 2px solid @c-main;
		&:hover {
			background-color: @c-main;
		}
	}
	&--orange {
		background-color: @c-main;
		border: 2px solid @c-main;
		&:hover {
			background-color: transparent;
		}
	}
}
.wrapper {
	min-height: 100%;
	position: relative;
	overflow: hidden;
}
.container {
	width: 100%;
	max-width: 1310px;
	margin: 0 auto;
	padding-right: 15px;
	padding-left: 15px;
	@media(max-width: 1023px) {
		max-width: 950px;
		padding-right: 35px;
		padding-left: 35px;
	}
	@media(max-width: 450px) {
		padding-right: 8px;
		padding-left: 8px;
	}
}
.flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}
.img-responsive {
	width: 100%;
	height: auto;
}
.lock {
	overflow: hidden;
}