.mobile-menu {
	display: none;
	position: absolute;
	top: -5000px;
	left: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	height: 100vh;
	padding: 10px 8px;
	overflow: auto;
	background-color: #25262a;
	z-index: 10;
	transition: 0.5s;
	&__active {
		top: 0;
		display: flex;
	}
}